import { environment } from '../../environments/environment'


export class Memory {
  public static userId: string
  public static loading = false
  public static url = '/api'
  public static apiUrl: string = environment.API_URL

  public static getToken() {
    const token = localStorage.getItem('sanay.token')
    return token
  }

  public static setToken(token: string) {
    localStorage.setItem('sanay.token', token)
  }

  public static getUser() {

    const user = localStorage.getItem('sanay.user')
    return JSON.parse(user)
  }

  public static setUser(user: any) {
    localStorage.setItem('sanay.user', JSON.stringify(user))
  }

  public static getRefreshToken() {
    const token = localStorage.getItem('sanay.refreshToken')
    return token
  }

  public static logout() {
    localStorage.removeItem('sanay.refreshToken')
    localStorage.removeItem('sanay.token')
    localStorage.removeItem('sanay.user')
  }

  public static setRefreshToken(token: string) {
    localStorage.setItem('sanay.refreshToken', token)
  }

  public static setLoading(state) {
    this.loading = state
  }

  public static getUserId() {
    return this.userId
  }

  public static getUrl() {
    return this.url
  }

  public static getApiUrl() {
    return this.apiUrl
  }

  public static getUserIdStorage() {
    const id = localStorage.getItem('sanay.user_id')
    return id
  }
}
