import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { MainRoutingModule } from './main-routing.module';
import { LandingComponent } from './landing/landing.component';
import {CoreModule} from '../../core/core.module';
import {CarouselModule} from 'ngx-owl-carousel-o';
import {SlickCarouselModule} from 'ngx-slick-carousel';
import { ContactUsComponent } from './contact-us/contact-us.component';
import { AboutUsComponent } from './about-us/about-us.component';
import {MatFormFieldModule} from '@angular/material/form-field';
import {MatInputModule} from '@angular/material/input';
import { ScrollToModule } from '@nicky-lenaers/ngx-scroll-to';
import { ServicesComponent } from './services/services.component';
import {ProductService} from '../../services/product.service';
import {MainServicesService} from '../../services/mainServices.service';
import {ClientService} from '../../services/client.service';
import { AllServicesComponent } from './all-services/all-services.component';
import {MatTooltipModule} from "@angular/material/tooltip";
import { NewLandingComponent } from './new-landing/new-landing.component';
import {MatExpansionModule} from '@angular/material/expansion';


@NgModule({
  declarations: [LandingComponent, ContactUsComponent, AboutUsComponent, ServicesComponent, AllServicesComponent, NewLandingComponent],
    imports: [
        CommonModule,
        MainRoutingModule,
        CoreModule,
        CarouselModule,
        SlickCarouselModule,
        MatFormFieldModule,
        MatInputModule,
        ScrollToModule.forRoot(),
        MatTooltipModule,
        MatExpansionModule
    ],
  providers: [
    ProductService,
    MainServicesService,
    ClientService
  ]
})
export class MainModule { }
