import { Component, OnInit } from '@angular/core';
import {MainService} from '../../../modals/mainService';
import {MainServicesService} from '../../../services/mainServices.service';

@Component({
  selector: 'app-all-services',
  templateUrl: './all-services.component.html',
  styleUrls: ['./all-services.component.scss']
})
export class AllServicesComponent implements OnInit {
  loading : boolean = true;
  allMainService:Array<MainService>=[]

  constructor(private mainService: MainServicesService,) { }

  ngOnInit(): void {
    this.getAllMainService()
  }

  getAllMainService(){
    this.mainService.getAll().subscribe((res:any) =>{
      if(res.data){
        this.loading = false;
        this.allMainService = res.data;
      }
    })
  }
}
