import { Component, OnInit } from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {MainService} from '../../../modals/mainService';
import {MainServicesService} from '../../../services/mainServices.service';

@Component({
  selector: 'app-services',
  templateUrl: './services.component.html',
  styleUrls: ['./services.component.scss']
})
export class ServicesComponent implements OnInit {
  id;
  data:MainService = new MainService()
  allMainService:Array<MainService>=[]
  index;
  loading: boolean = true;
  constructor(private route:ActivatedRoute,
              private mainService: MainServicesService,
  ) { }

  ngOnInit(): void {
    this.route.params.subscribe(params =>{
      if(params['id']){
        this.id = params['id']
        this.getMainService()
        this.getAllMainService()
      }
    })
  }

  getAllMainService(){
    this.mainService.getAll().subscribe((res:any) =>{
      if(res.data){
        this.loading = false;
        this.allMainService = res.data;
        for (let item of this.allMainService){
          if(item._id === this.id){
            this.index = this.allMainService.indexOf(item)
          }
        }
        this.allMainService.splice(this.index,1)
      }
    })
  }
  getMainService(){
    this.mainService.getOne(this.id).subscribe((res:any) =>{
      if(res.data){
        this.loading = false;
        this.data = res.data
      }
    })
  }
}
