import { Component, OnInit,AfterViewInit,HostListener, Inject } from '@angular/core';
import * as AOS from 'aos';
import {Client} from "../../../modals/client";
import {ClientService} from "../../../services/client.service";
import {Product} from "../../../modals/product";
import {ProductService} from "../../../services/product.service";
import { trigger, state, transition, style, animate } from '@angular/animations';
import { DOCUMENT } from '@angular/common';

@Component({
  selector: 'app-new-landing',
  templateUrl: './new-landing.component.html',
  styleUrls: ['./new-landing.component.scss'],
  animations:[
    trigger('fade',
        [
          state('void', style({ opacity : 0})),
          transition(':enter',[ animate(300)]),
          transition(':leave',[ animate(500)]),
        ]
    )]
})
export class NewLandingComponent implements OnInit , AfterViewInit {
  allClient:Array<Client>=[]
  loading:boolean = true;
  clientDetails: Client = new Client();
  panelOpenState = false;
  allProduct:Array<Product>= []

  constructor(private clientService: ClientService,
              private service: ProductService,
              @Inject(DOCUMENT) document) { }

  ngOnInit(): void {
    AOS.init();
    this.getClients()
    this.getProducts()
  }

  @HostListener('window:scroll', ['$event'])
  onWindowScroll(e) {
    if (window.pageYOffset > 20) {
      let element = document.getElementById('header');
      element.classList.add('sticky');
    } else {
      let element = document.getElementById('header');
      element.classList.remove('sticky');
    }
  }
  ngAfterViewInit() {

  }

  getClients(): any{
    this.clientService.getAll().subscribe((res:any) =>{
      if(res.data) {
        this.loading = false;
        this.allClient = res.data;
      }
    })
  }

  getProducts(){
    this.service.getAll().subscribe((res:any) =>{
      if(res.data){
        this.loading = false
        this.allProduct = res.data;
      }
    })
  }
}
