import {AfterViewInit, Component, ElementRef, OnInit} from '@angular/core';
import {Product} from '../../../modals/product';
import {ProductService} from '../../../services/product.service';
import {MainServicesService} from '../../../services/mainServices.service';
import {MainService} from '../../../modals/mainService';
import {ClientService} from '../../../services/client.service';
import {Client} from '../../../modals/client';

declare var $;
@Component({
  selector: 'app-landing',
  templateUrl: './landing.component.html',
  styleUrls: ['./landing.component.scss'],
})
export class LandingComponent implements OnInit, AfterViewInit {
  allProduct:Array<Product>= []
  allMainService:Array<MainService>=[]
  allClient:Array<Client>=[]
  clientDetails: Client = new Client();
  loading:boolean = true;
  constructor(private elementRef: ElementRef,
              private mainService: MainServicesService,
              private clientService: ClientService,
              private service: ProductService,) {
    const s = document.createElement('script');
    s.type = 'text/javascript';
    s.src = '/assets/js/main.js';
    this.elementRef.nativeElement.appendChild(s);
  }
  ngAfterViewInit(): void {
    runslide();

    function runslide() {
      $('#first').fadeIn(1500).delay(3500).fadeOut(1500, function() {
          $('#second').fadeIn(1500).delay(3500).fadeOut(1500, function() {
              runslide();
          });
      });
  }
  }
  ngOnInit(): void {
    this.getProducts()
    this.getMainItems()
    this.getClients()
  }

  getProducts(){
    this.service.getAll().subscribe((res:any) =>{
      if(res.data){
        this.loading = false
        this.allProduct = res.data;
      }
    })
  }

  getMainItems(){
    this.mainService.getAll().subscribe((res:any) =>{
      if(res.data) {
        this.loading = false;
        this.allMainService = res.data
      }
    })
  }

  getClients(){
    this.clientService.getAll().subscribe((res:any) =>{
      if(res.data) {
        this.loading = false;
        this.allClient = res.data;
        this.clientService.getOne(this.allClient[0]._id).subscribe((res:any) =>{
          this.clientDetails = res.data
        })
      }
    })
  }

  clientDetail(id) {
    this.clientService.getOne(id).subscribe((res:any) =>{
      this.clientDetails = res.data
    })
  }
}
